.tabs {
  display: flex;
  gap: 0px;
  border-bottom: 1px solid #191c21;
  margin-bottom: 16px;
  padding: 0 4px;
}

.tab {
  padding: 16px 8px;
  border: none;
  background: transparent;
  color: #9d9fa3;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
  font-family: "Lato", sans-serif;
  min-width: 64px;
  width: 100%;
  text-align: center;
}

.tab::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
  height: 1px;
  background: #191c21;
  transition: all 0.2s ease;
}

.tab:hover {
  color: #fff;
}

.tab:hover::after {
  background: #2a2d35;
}

.tab.active {
  color: #ffffff;
}

.tab.active::after {
  height: 2px;
  background: #9e0676;
  border-radius: 2px 2px 0 0;
}
