.right-drawer {
  position: fixed;
  top: 0;
  right: -640px;
  width: 640px;
  height: 100vh;
  background-color: var(--color-neutral-850);
  border-left: 1px solid var(--color-neutral-700);
  z-index: 1000;
  transition: right 0.3s ease-in-out;
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.right-drawer.open {
  right: 0;
}

.drawer-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid var(--color-neutral-700);
}

.drawer-title {
  font-size: 22px;
  font-weight: 600;
  color: #fff;
  font-family: "Lato", sans-serif;
}

.drawer-sections {
  flex: 1;
  overflow-y: auto;
  padding: 32px;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.right-drawer-section {
  background: none;
  border-radius: 8px;
  padding: 0;
  margin-bottom: 0;
}

.section-title {
  font-size: 18px;
  font-weight: 500;
  color: #fff;
  margin-bottom: 4px;
  font-family: "Lato", sans-serif;
}

.section-desc {
  color: var(--color-neutral-400);
  font-size: 14px;
  margin-bottom: 16px;
  font-family: "Lato", sans-serif;
}

.section-row {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-direction: row;
}

.empty-state {
  flex: 1;
  height: 48px;
  border: 2px dashed var(--color-neutral-600);
  border-radius: 8px;
  background: none;
  min-width: 0;
  cursor: pointer;
  display: flex;
  transition: all 0.3s ease;
  overflow: hidden;
}

.empty-state.pulsing {
  animation: pulse 1.5s infinite;
  border-color: var(--color-primary-500);
}

.filled-state {
  flex: 1;
  height: 48px;
  border: none;
  border-radius: 8px;
  background: none;
  min-width: 0;
  display: flex;
  overflow: hidden;
  padding: 0;
}

.filled-state .color-swatch {
  border: none !important;
  border-radius: 0 !important;
}

.dashed-rectangle {
  flex: 1;
  height: 48px;
  border: 2px dashed var(--color-neutral-600);
  border-radius: 8px;
  background: none;
  min-width: 0;
  cursor: pointer;
  display: flex;
  transition: all 0.3s ease;
  overflow: hidden;
}

.dashed-rectangle.pulsing {
  animation: pulse 1.5s infinite;
  border-color: var(--color-primary-500);
}

.dashed-rectangle.saving-mode {
  cursor: pointer;
}

.dashed-rectangle:not(.saving-mode) {
  cursor: default;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(23, 115, 207, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(23, 115, 207, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(23, 115, 207, 0);
  }
}

.dashed-rectangle .color-swatch {
  height: 100%;
  transition: all 0.3s ease;
  flex: 1;
  min-width: 0;
}

.dashed-rectangle .color-swatch:hover {
  filter: brightness(1.1);
}

.drawer-actions {
  display: flex;
  align-items: center;
  gap: 8px;
}
