/* Dashed rectangle for placeholder ramps in Custom tab */
.dashed-rectangle {
  height: 48px;
  width: 100%;
  border: 1px dashed #666a70;
  border-radius: 6px;
  margin: 8px 0;
  background: none;
  box-sizing: border-box;
  opacity: 0.7;
  transition: background 0.18s, border-color 0.18s;
}

.dashed-rectangle:hover,
.dashed-rectangle:focus {
  background: none;
  border-color: #9e0676;
  opacity: 1;
  outline: none;
}

.dashed-rectangle:active {
  background: #191c21;
  border-color: #7d025c;
  opacity: 1;
}

.dashed-rectangle-add-icon {
  width: 24px;
  height: 24px;
  display: block;
  fill: #666a70;
}
